.contact h1 {
    font-size: 30px;
    border-bottom: 2px solid black;
    width: fit-content;
    margin: 0px auto;
    font-weight: 1000;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2%;
  }
  .contact {
    width: 100%;
    /* background: rgb(208, 199, 203);
    background: linear-gradient(
      351deg,
      rgba(208, 199, 203, 1) 25%,
      rgba(126, 122, 125, 1) 55%,
      rgba(120, 122, 125, 1) 84%
    ); */
    background-color: rgb(179, 179, 179);
    color: black !important;
    height: 40vh;
  }
  .top {
    width: 40%;
    margin: 3% auto;
    justify-content: space-between;
  }
  .top .insta,
  .top .link,
  .top .git {
    font-size: 50px;
    margin: 0px 10%;
    color: black;
  }
  .top .insta:hover {
    color: purple;
  }
  .top .git:hover {
    color: green;
  }
  .top .link:hover {
    color: blue;
  }
  .det {
    display: flex;
    margin: 0px auto;
    text-align: center;
    width: fit-content;
  }
  .det * {
    margin: 10px 10px;
  }
  .phone::before {
    content: "|";
  }